body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100%;
  width: 100%;
}

.background {
  height: 100vh;
  width: 100%;
  position: fixed;
}

.container {
  position: fixed;
  -webkit-perspective: 2500px;
          perspective: 2500px;
  background: transparent;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  perspective: 2500px;
  width: 100%;
  height: 100%;
}

.bounce {
  -webkit-animation: bounce 12s cubic-bezier(0.175, 0.885, 0.32, 1.275) infinite
    alternate;
          animation: bounce 12s cubic-bezier(0.175, 0.885, 0.32, 1.275) infinite
    alternate;

  /* animation: bounce 12s ease infinite alternate; */
  width: 100%;
  height: 56.25vw;
}

.scale {
  -webkit-animation: scale 7s cubic-bezier(0.25, 0.46, 0.45, 0.94) infinite alternate;
          animation: scale 7s cubic-bezier(0.25, 0.46, 0.45, 0.94) infinite alternate;
  width: 100%;
  height: 56.25vw;
}

.bbq {

  /* animation: bbq 7s linear infinite alternate; */
  width: 100%;
  height: 56.25vw;
}

.spin {
  width: 100%;
  height: 56.25vw;
  -webkit-animation: spin 15s linear infinite;
          animation: spin 15s linear infinite;
}

@-webkit-keyframes zoom {
  from {
    -webkit-perspective: 500px;
            perspective: 500px;
  }

  to {
    -webkit-perspective: 1000px;
            perspective: 1000px;
  }
}

@keyframes zoom {
  from {
    -webkit-perspective: 500px;
            perspective: 500px;
  }

  to {
    -webkit-perspective: 1000px;
            perspective: 1000px;
  }
}

@-webkit-keyframes scale {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0.2;
  }

  to {
    -webkit-transform: scale(2);
            transform: scale(2);
    opacity: 1;
  }
}

@keyframes scale {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0.2;
  }

  to {
    -webkit-transform: scale(2);
            transform: scale(2);
    opacity: 1;
  }
}

@-webkit-keyframes bounce {
  from {
    -webkit-transform: translateY(-300px);
            transform: translateY(-300px);
  }

  to {
    -webkit-transform: translateY(300px);
            transform: translateY(300px);
  }
}

@keyframes bounce {
  from {
    -webkit-transform: translateY(-300px);
            transform: translateY(-300px);
  }

  to {
    -webkit-transform: translateY(300px);
            transform: translateY(300px);
  }
}

@-webkit-keyframes bbq {
  from {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
  }

  to {
    -webkit-transform: rotateX(360deg);
            transform: rotateX(360deg);
  }
}

@keyframes bbq {
  from {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
  }

  to {
    -webkit-transform: rotateX(360deg);
            transform: rotateX(360deg);
  }
}

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);

    /* , scale(2, 2); */
  }

  to {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);

    /* , scale(1, 2); */
  }
}

@keyframes spin {
  from {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);

    /* , scale(2, 2); */
  }

  to {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);

    /* , scale(1, 2); */
  }
}



